
.page-aboutme{padding-bottom:20px
}
.wn-aboutme-box{background:#fff;border:1px solid #cfcfcf;padding:15px 20px 35px;margin:20px 20px 10px;font-size:32px;color:#333
}
.wn-aboutme-box .aboutme-tit{font-size:36px;font-weight:bold;padding-bottom:8px;border-bottom:1px dashed #f60;margin-bottom:15px
}
.aboutme-zj a{text-decoration:underline;color:#339dd2
}
.aboutme-zj li{padding:15px 15px;border-bottom:1px dashed #e5e5e5
}
.aboutme-zj li:last-child{border-bottom:none
}
.aboutme-zj li p{font-size:32px;line-height:1.5;margin-bottom:5px
}
.aboutme-prolist{font-size:32px
}
.aboutme-prolist li{padding:15px 10px;border-bottom:1px dashed #e5e5e5
}
.aboutme-prolist li:last-child{border-bottom:none
}
.aboutme-prolist li .item{position:relative;padding-left:1.46667rem;margin-bottom:5px
}
.aboutme-prolist li .item a{text-decoration:underline;color:#339dd2;display:inline-block;margin-right:10px
}
.aboutme-prolist .lab{position:absolute;left:0;top:0;width:1.33333rem;text-align:right
}
.aboutme-table-box{padding:20px 30px
}
.aboutme-table{border:1px solid #e6e6e6;width:100%
}
.aboutme-table th{padding:20px 0;text-align:center
}
.aboutme-table .address{font-size:16px;line-height:2
}
.aboutme-table .address p{text-indent:2em
}
.aboutme-table td.icon{width:50px
}
.aboutme-table td.date{font-family:arial;font-size:18px;color:#666;width:80px
}
.aboutme-table td.icon span{font-size:40px;color:#f60
}
.aboutme-table td{padding:15px 10px;border:1px solid #f60
}
.aboutme-table td.tit{width:90px;text-align:right;font-weight:bold
}
.aboutme-intro{line-height:1.8
}
.aboutme-intro p{text-indent:2em;margin-bottom:15px
}
.aboutme-table-project{font-size:16px;line-height:2
}
.aboutme-table-project .center{text-align:center
}
.aboutme-table-project a{text-decoration:underline
}
.aboutme-table-project p{text-indent:2rem
}



