
.box-main{margin:30px 20px 0
}
.log-list li{border:1px solid #cfcfcf;background:#fff;padding:15px 20px 65px;margin-bottom:20px;position:relative
}
.log-list li .tit{font-size:42px;word-break:break-all;font-weight:normal
}
.log-list li .tit i{color:#e96363;font-size:80px;padding:0;vertical-align:middle;height:.8rem;line-height:.66667rem;overflow:hidden;display:inline-block
}
.log-list li .tit .zd{color:#e96363;margin-right:5px
}
.log-list li .readmore{position:absolute;color:#fff;right:20px;bottom:15px;padding:12px 28px;border-radius:4px;font-size:28px;border-color:#d43f3a;background-color:#d9534f
}
.log-list li .readmore:hover{background-color:#c9302c;border-color:#ac2925
}
.log-list li .readmore:active{box-shadow:inset 0 3px 5px rgba(0,0,0,0.125)
}
.log-list li .entry-meta{color:#999;margin-top:5px;padding-bottom:2px;border-bottom:1px solid #dedede
}
.log-list li .entry-meta a{color:#999
}
.log-list li .entry-meta a:hover{text-decoration:underline
}
.log-list li .entry-meta span{font-size:28px;margin-right:20px
}
.log-list li .entry-meta span i{margin-right:5px;vertical-align:middle
}
.log-list li .entry-meta .discuss{margin-right:0;float:right
}
.log-list li .desc{padding:15px 10px;word-break:break-all;line-height:1.5;font-size:32px;color:#666
}
.log-list li .desc p{text-indent:2em;margin-bottom:10px
}



