
.content-container{border:1px solid #cfcfcf;background:#fff;padding:15px 20px;margin-bottom:30px;margin:30px 20px 0
}
.dp-tit{color:#333;font-size:48px;font-weight:normal
}
.dp-bar{color:#999;margin-top:5px;padding-bottom:2px;border-bottom:1px solid #dedede
}
.dp-bar a{color:#999
}
.dp-bar span{font-size:28px;margin-right:20px
}
.dp-bar a:hover{text-decoration:underline
}
.dp-bar i{margin-right:5px;vertical-align:middle
}
.dp-bar .discuss{margin-right:0;float:right
}
.dp-content{padding:25px 10px 10px;font-size:34px;color:#2b2b2b;line-height:1.5;word-wrap:break-all;word-break:break-all;overflow:hidden
}
.dp-content img{width:auto;max-width:100% !important
}
.dp-content p{margin-bottom:15px
}
.dp-content .tit-1{font-size:36px;font-weight:bold
}
.dp-content .tit-2{margin-bottom:5px;font-size:16px;text-indent:2rem;font-weight:bold
}
.dp-content .con-1{text-indent:2rem
}
.dp-content .con-2{text-indent:4rem
}
.dp-content .dp-table{border:1px solid #e5e5e5;width:100%;overflow:hidden;table-layout:fixed;word-break:break-all;word-wrap:break-word
}
.dp-content .dp-table th,.dp-table td{border:1px solid #e5e5e5;padding:5px;text-align:center
}
.dp-content .dp-table th{background:#eeeeee
}
.dp-content .dp-table .td-left{text-align:left;padding-left:10px
}
.dp-runcodetextarea{border:1px solid #dedede;width:98%;margin-bottom:5px;min-height:230px;padding:10px 0 10px 2%;color:#666
}
.dp-share{text-align:right
}
.dp-share-detail{display:inline-block;font-size:0;min-width:155px
}
.dp-dir{padding:15px 0 0;border-top:1px solid #dedede
}
.dp-dir p{line-height:25px;color:#666
}
.dp-dir p a:hover{text-decoration:underline
}
.dp-comment{border-top:1px solid #dedede
}
.dp-comment{margin:20px 0 15px;border:1px solid #cfcfcf;background:#fff;padding:15px 20px
}
.dp-readnum b{padding:0 2px;font-size:14px;font-family:Georgia;color:#e96363;font-weight:normal
}
.dp-exampleBtn{margin-bottom:10px
}
.dp-exampleBtn a{margin:0 5px
}
.dp-exampleBtn-l{text-align:left
}
.dp-exampleBtn-c{text-align:center
}
.dp-exampleBtn-r{text-align:right
}
.dp-exampleBtn a{margin-bottom:10px;color:#fff
}
.syntaxhighlighter{margin-top:0 !important
}
.syntaxhighlighter .gutter,.syntaxhighlighter .code{padding:15px 0 !important
}
.bdshare-button-style0-24 .bds_more{display:none
}



