
.page-box{-webkit-transition:-webkit-transform 250ms ease-out;top:0;width:100%;position:absolute;background:#e5e5e5
}
.page-box.open{transform:translate3d(-50%, 0px, 0px);margin-left:1px
}
.page-box .wn-drawer-mask{position:fixed;left:0;top:0;bottom:0;right:0;background-color:rgba(0,0,0,0.8);visibility:hidden;z-index:10;opacity:0
}
.page-box .wn-drawer-mask.open{opacity:1;visibility:visible;-webkit-transition:all 250ms ease-out
}
.page-home.open .wn-drawer{display:block
}



