
@import url(../static/iconfont/iconfont.css);
@import url(../static/css/reset.css);
html{height:100%
}
body{height:100%
}
.app{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;width:100%;height:100%;top:0;position:absolute;font-family:'Avenir', Helvetica, Arial, sans-serif
}
.animated{animation-duration:0.25s;animation-fill-mode:both
}
@keyframes slideInLeft{
0%{transform:translate3d(-100%, 0, 0);visibility:visible
}
100%{transform:translate3d(0, 0, 0)
}
}
.slideInLeft{animation-name:slideInLeft
}
@keyframes slideOutLeft{
0%{transform:translate3d(0, 0, 0)
}
100%{visibility:hidden;transform:translate3d(-100%, 0, 0)
}
}
.slideOutLeft{animation-name:slideOutLeft
}
@keyframes slideInRight{
0%{transform:translate3d(100%, 0, 0);visibility:visible
}
100%{transform:translate3d(0, 0, 0)
}
}
.slideInRight{animation-name:slideInRight
}
@keyframes slideOutRight{
0%{transform:translate3d(0, 0, 0)
}
100%{visibility:hidden;transform:translate3d(100%, 0, 0)
}
}
.slideOutRight{animation-name:slideOutRight
}
@keyframes fadeIn{
0%{opacity:0
}
100%{opacity:1
}
}
.fadeIn{animation-name:fadeIn
}
@keyframes fadeOut{
0%{opacity:1
}
100%{opacity:0
}
}
.fadeOut{animation-name:fadeOut
}



